import {BaseRecord } from './Models/BaseRecord';
import { SelectItem } from './Models/SelectItem';
import { Injectable } from '@angular/core';

import { FoodItem } from './Models/FoodItem'
import { DrugItem } from './Models/DrugItem'
import { urlItem } from './Models/urlItem'

@Injectable()
export class RecordService{
    private records: Array<BaseRecord> = [];
    private rt: Array<SelectItem> = [];
    private units: Array<SelectItem> = [];
    private brands: Array<SelectItem> = [];
    private foodItems: Array<FoodItem> = [];

    private drugItems: Array<DrugItem> = [];
    get DrugItems(){ return this.drugItems }

    public FechaHoy: Date;
    public Hora: string = "";
    public RecType: string = "FOOD";
    public EnableSave: boolean = false;

    
    private _message : string;
    public get message() : string {
        return this._message;
    }
    public set message(v : string) {
        this._message = v;
    }

    
    private _isError : boolean;
    public get isError() : boolean {
        return this._isError;
    }
    public set isError(v : boolean) {
        this._isError = v;
    }

    public brandItems: Array<urlItem>=[]
    
    //genric finder     
    getUrl( coll: Array<urlItem>, id: string){
        return coll.filter(x=> x.id == id )[0].url;
    }
    getBrand(brandId: string)
    {
        let x = this.brandItems.filter(x => x.id == brandId)[0];
        if ( !x )
            x = new urlItem("","","")
        return x;
    }
    
    getName(name: string, id: number)
    {
        return name + id.toString();
    }


    initialize(){

        this.FechaHoy = new Date();
        this.Hora = `${this.FechaHoy.getHours()}:${this.FechaHoy.getMinutes()}`;
        this.Load();
        this.message = "Ready";
        this.loadBrands();
        this.loadDrugItems();

    }


    loadBrands()
    {
        this.brandItems = [];

        this.brandItems.push(new urlItem("CNP","Conaprole",""));
        this.brandItems.push(new urlItem("COK","Coca Cola",""));
        this.brandItems.push(new urlItem("PAT","Patricia",""));
        this.brandItems.push(new urlItem("LAY","Lays",""));


        this.brandItems.push(new urlItem("DISCO","DISCO","https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT-cavcNMeRzLdzfFTmbbPVQHAd-KFzRtaQWl99fDZnwPm_2P49Lg&s"))

        this.brandItems.push(new urlItem("CNP","Conaprole","data:image/png;base64,"))

    }


    constructor(){
        this.initialize();

    }


    Add(r: BaseRecord):number
    {
        r.Id = this.records.length;
        this.records.push(r);
        this.EnableSave = true;
        this.message = `Record Added:${r.RecType} ${r.Id}`
        console.log(this.message);
        return r.Id;
    }

    Load(){
        let serialized = localStorage.getItem("data");
        if ( serialized != null )
        {
            this.records = JSON.parse(serialized);
            this.message = `Data Loaded. ${this.records.length} records`;
        }
    }

    
    GetRecords() :Array<BaseRecord>{
        return this.records;
    }

    get Brands():Array<SelectItem>{
        return this.brands;
    }
    get RecTypes():Array<SelectItem>{
        this.rt = [];
        this.rt.push(new SelectItem("","Select Record Type"));
        this.rt.push(new SelectItem("FOOD","Food"));
        this.rt.push(new SelectItem("DRUG","Drug"));
        this.rt.push(new SelectItem("GLUC","Glucose"));
        this.rt.push(new SelectItem("PRES","Pressure"));
        this.rt.push(new SelectItem("WGT","Weight"));
        this.rt.push(new SelectItem("EXE","Exercise"));
        //this.rt.push(new SelectItem("$EX","Expenses"));
        //this.rt.push(new SelectItem("$IN","Incomes"));
    
        return this.rt;
    }

    get Units():Array<SelectItem>{
        this.units = [];
        this.units.push(new SelectItem("GR","Grams"));
        this.units.push(new SelectItem("mg","MiliGrams"));
        this.units.push(new SelectItem("ml","Mililiters"));
        this.units.push(new SelectItem("OZ","Ounces"));
        this.units.push(new SelectItem("LT","Liter"));
        this.units.push(new SelectItem("LB","Pound"));
        this.units.push(new SelectItem("uL","uL"));
        this.units.push(new SelectItem("U","Unit"));
        return this.units;
    }

    loadDrugItems():Array<DrugItem>{
        this.drugItems = [];
        this.drugItems.push(new DrugItem("DIAF","Diaformina",850,"ml","ROEMMERS","data:image/jpeg;base64,"))
        this.drugItems.push(new DrugItem("CORIZAN","Corizan",50,"uL","","data:image/jpeg;base64,"))
        this.drugItems.push(new DrugItem("CRESTOR","Crestor Rosuvastatina",10,"mg","AstraZeneca","data:image/jpeg;base64,"))        
        
        this.drugItems.push(new DrugItem("DIARIS","DIARIS",1,"U","Poen","data:image/jpeg;base64,"))
        this.drugItems.push(new DrugItem("TAROPLEN","Taroplen (micardis)",40,"mg","Lazar","data:image/jpeg;base64,"))
        this.drugItems.push(new DrugItem("NOVONORM","Novonorm",1,"mg","novo nordisk","data:image/jpeg;base64,"))
        this.drugItems.push(new DrugItem("LORATADINA","Loratadina",10,"mg","Urufarma","data:image/jpeg;base64,"))
         return this.drugItems;
    }

    get FoodItems():Array<FoodItem>{
        this.foodItems = [];
        //todo: fake list
        this.foodItems.push(new FoodItem(null,"Select Item"));
        this.foodItems.push(new FoodItem("EGG","Huevos",1,"U","DISCO","https://as01.epimg.net/deporteyvida/imagenes/2017/06/23/portada/1498239720_938816_1498240077_noticia_normal.jpg"));
        this.foodItems.push(new FoodItem("BRD","Pan frances",1,"U","DISCO","https://img.vixdata.io/pd/webp-large/es/sites/default/files/imj/elgranchef/p/pan-frances.jpg"));
        this.foodItems.push(new FoodItem("RCE","Arroz",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("MET","Carne",200,"GR","DISCO","https://www.purina.com/sites/g/files/auxxlc196/files/styles/facebook_share/public/purina-can-dogs-eat-real-meat-500x300.jpg?itok=IJ3xiqyx"));
        this.foodItems.push(new FoodItem("CHK","Pollo",200,"GR","DISCO","https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRsBwYoGxaKZ776G-nkjFu6BcbZcWGpionqP3lDY81NuG8aD-YWRg&s"));
        this.foodItems.push(new FoodItem("FF","French Fries",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("WAT","Water",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("TUN","Tuna",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("SAL","Salad",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("MIC","Milanesa Carne",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("CHV","Chivito",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("LMY","Lemeyun",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("PP1","Papas Fritas bolsa",100,"GR","DISCO","https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRltZFxWiId2nh1JQAYGKQnOhM4qSZB6wgBuPfz8swyPdRf7afmDA&s"));
        this.foodItems.push(new FoodItem("PP2","Papas Fritas Tubo",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("CRP","Creps",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("CAN","Canelones",100,"GR","DISCO","data:image/jpeg;base64,"));
        this.foodItems.push(new FoodItem("LAS","Lasagna",100,"GR","DISCO","data:image/jpeg;base64,"));

        return this.foodItems;
    }

    GetSelectFoodItem(id: string): FoodItem{
        this.EnableSave = false;
        return this.foodItems.filter(x => x.Id == id)[0];
    }

    GetSelectDrugItem(id: string): DrugItem{
        this.EnableSave = false;
        return this.drugItems.filter(x => x.Id == id)[0];
        

        // let x = new DrugItem("","")
        // for(let item of this.drugItems )
        // {
        //     if ( item.Id == id )
        //     {
        //         x = item;
        //         console.log('drug item found:', x)
        //         break;
        //     }
        // }
        // return x;
    }

    GetSelectItem(id: string): SelectItem{
        //TODO: THIS SHOULD NOT BE NECESARY, SHOULD BE LOADED BY TWO WAY BINDING AND IS NOT DOING SO
        //this.RecType = id;
        console.log(`"GetSelectedItem:${id}"`)
        this.EnableSave = false;
        return this.rt.filter(x => x.Id == id)[0];
    }

    //todo: make load and save async await
    Save():number
    {
        let serialized = JSON.stringify(this.records);
        localStorage.setItem("data", serialized);
        this.message = `Data Saved, total records: ${this.records.length}`;
        console.log(this.message);
        console.log(serialized);
        this.EnableSave = false;
        return this.records.length
    }

    get Data() :string{
        return JSON.stringify(this.records);

    }

    clearData(){
        this.records = [];
        this.Save();
        this.message = "Data was cleared."
    }
}
